.btn_standart {
  transition: all .2s;
  display: block;
  cursor: pointer;
  width: -moz-fit-content;
  width: fit-content;
  letter-spacing: normal;
  font-weight: 500;
  font-size: 18px;
  letter-spacing: 2px;
  padding: 10px 25px;
  min-width: 170px;
  text-align: center;
  border-radius: 3px; }
  .btn_standart_yellow {
    color: #fff;
    background: #D57B00;
    border: solid 1px #D57B00; }
    .btn_standart_yellow:hover {
      border: 1px solid #975c08;
      background: #975c08; }
  .btn_standart_black {
    color: #fff;
    border: solid 1px transparent !important;
    background: #333333; }
    .btn_standart_black:hover {
      border: solid 1px #DB1A1A !important;
      background: #DB1A1A; }
    .btn_standart_black_2 {
      color: #595959;
      border: solid 1px #595959;
      background: transparent; }
      .btn_standart_black_2:hover {
        border: solid 1px #DB1A1A;
        background: #DB1A1A; }
