  .header_navigation {
    transition: all .3s;
    position: relative;
    top: -5px; }
    .header_navigation_ul {
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center; }
      .header_navigation_ul li {
        transition: all .2s;
