        align-items: center;
        width: 100%; }
        .header_navigation_ul a img {
          margin-left: 4px; }
    .header_navigation_link {
      position: relative; }
      .header_navigation_link_a {
        transition: all .2s;
        border-bottom: 1px solid transparent;
        padding-bottom: 3px; }
        .header_navigation_link_a:hover {
          border-bottom: 1px solid #fdd03b;
          color: #fdd03b; }
      .header_navigation_link img {
