      .header_navigation_sub_link a {
        transition: all .2s;
        display: block;
        color: #00202e; }
      .header_navigation_sub_link:hover {
        background: #00202e93; }
        .header_navigation_sub_link:hover a {
          color: #fff; }

.wrapper {
  background: transparent;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 999; }

.bg-bubbles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1; }

.bg-bubbles li {
  position: absolute;
  list-style: none;
  display: block;
  width: 40px;
  height: 40px;
  background-color: #00202e4f;
  bottom: -160px;
  -webkit-animation: square 25s infinite;
  animation: square 25s infinite;
  -webkit-transition-timing-function: linear;
  transition-timing-function: linear; }

.bg-bubbles li:nth-child(1) {
  left: 10%; }

.bg-bubbles li:nth-child(2) {
  left: 20%;
  width: 80px;
  height: 80px;
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  -webkit-animation-duration: 19s;
  animation-duration: 19s; }

.bg-bubbles li:nth-child(3) {
  left: 25%;
  -webkit-animation-delay: 4s;
  animation-delay: 4s; }

.bg-bubbles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  -webkit-animation-duration: 24s;
  animation-duration: 24s;
  background-color: rgba(255, 255, 255, 0.25); }

.bg-bubbles li:nth-child(5) {
  left: 70%; }

.bg-bubbles li:nth-child(6) {
  left: 80%;
  width: 120px;
  height: 120px;
  -webkit-animation-delay: 3s;
  animation-delay: 3s;
  background-color: rgba(255, 255, 255, 0.2); }

.bg-bubbles li:nth-child(7) {
  left: 32%;
  width: 160px;
  height: 160px;
  -webkit-animation-delay: 7s;
  animation-delay: 7s; }

.bg-bubbles li:nth-child(8) {
  left: 55%;
  width: 20px;
  height: 20px;
  -webkit-animation-delay: 15s;
  animation-delay: 15s;
  -webkit-animation-duration: 42s;
  animation-duration: 42s; }

.bg-bubbles li:nth-child(9) {
  left: 25%;
  width: 10px;
  height: 10px;
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  -webkit-animation-duration: 42s;
  animation-duration: 40s;
  background-color: rgba(255, 255, 255, 0.3); }

.bg-bubbles li:nth-child(10) {
  left: 90%;
  width: 160px;
  height: 160px;
  -webkit-animation-delay: 11s;
  animation-delay: 11s; }

@-webkit-keyframes square {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  100% {
    -webkit-transform: translateY(-1500px) rotate(600deg);
    transform: translateY(-1500px) rotate(600deg); } }

@keyframes square {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  100% {
    -webkit-transform: translateY(-1500px) rotate(600deg);
    transform: translateY(-1500px) rotate(600deg); } }

.burger_menu {
  display: none;
  position: absolute;
  transform: scale(0.5);
  top: 15px;
  right: 10px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  cursor: pointer;
  overflow: hidden; }
  .burger_menu.active i:nth-of-type(2) {
    animation: line-two .5s ease forwards; }
  .burger_menu.active i:nth-of-type(3) {
    animation: line-three .5s ease forwards; }
  .burger_menu.active i:first-of-type, .burger_menu.active i:nth-of-type(4), .burger_menu.active i:last-of-type {
    width: 0;
    transition: width 0s 0.4s; }
  .burger_menu.closed i:first-of-type, .burger_menu.closed i:nth-of-type(4), .burger_menu.closed i:last-of-type {
    animation: line-other-reverse .5s ease; }
  .burger_menu.closed i:nth-of-type(2) {
    width: 100%;
    transform: translateY(14px) rotate(-45deg);
    animation: line-two-reverse .5s ease forwards; }
  .burger_menu.closed i:nth-of-type(3) {
    transform: scale(1) rotate(45deg);
    animation: line-three-reverse .5s ease forwards; }

i {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #cb7f07; }

@keyframes line-two {
  0% {
    transform: translateY(0) rotate(0); }
  59% {
    transform: translateY(0) rotate(0);
    width: 100%; }
  60% {
    width: 0;
    transform: translateY(14px) rotate(-45deg); }
  100% {
    width: 100%;
    transform: translateY(14px) rotate(-45deg); } }

@keyframes line-three {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(35) rotate(0); }
  60% {
    transform: scale(35) rotate(90deg); }
  100% {
    transform: scale(1) rotate(45deg); } }

@keyframes line-two-reverse {
  0% {
    width: 100%;
    transform: translateY(14px) rotate(-45deg); }
  49% {
    width: 0;
    transform: translateY(14px) rotate(-45deg); }
  50% {
    transform: translateY(0) rotate(0);
    width: 100%; }
  100% {
    transform: translateY(0) rotate(0); } }

@keyframes line-three-reverse {
  0% {
    transform: scale(1) rotate(45deg); }
  50% {
    transform: scale(35) rotate(90deg); }
  60% {
    transform: scale(35) rotate(0); }
  100% {
    transform: scale(1); } }

@keyframes line-other-reverse {
  0% {
    width: 0; }
  60% {
    width: 0; }
  100% {
    width: 100%; } }

@media (max-width: 992px) {
  .burger_menu {
    display: flex; }
  .header_title {
    top: 20px !important; }
  .header_dinamic__block_hidden .header_title {
    top: 30px !important; }
  nav.header_navigation.header_navigation.header_navigation {
    display: flex;
    position: absolute;
    top: -350px !important;
    right: 0;
    width: 100vw;
    height: 300px;
    background: linear-gradient(180deg, #00202F 14.58%, rgba(0, 32, 47, 0.87) 36.46%, rgba(0, 32, 47, 0) 100%);
    transition-delay: .2s; }
  .header_navigation_ul {
    width: 100%;
    padding-top: 20px;
    flex-direction: column;
    justify-content: flex-start; }
    .header_navigation_ul .sub_title_1 {
      font-size: 24px; }
  .header_navigation_sub__block {
    display: none; }
  .header_navigation_link img {
    display: none; }
  .header_dinamic__block .header_title {
    transition: all .2s; }
  .header_dinamic__block.active_menu {
    top: -20px !important; }
    .header_dinamic__block.active_menu .header_navigation.header_navigation.header_navigation.header_navigation {
      top: 10px !important; }
    .header_dinamic__block.active_menu .header_title {
      top: -60px !important; } }
