main {
  position: relative; }

.unselectable {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                    not supported by any browser */ }

.main_container {
  max-width: 1400px;
  background: #fff;
  margin: 0 auto;
  position: relative;
  z-index: 0; }

.container {
  padding: 0px 5%;
  width: 100%;
  background: #fff; }

.upper_text {
  text-transform: uppercase; }

.absolute_center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.sub_title {
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 400; }
  .sub_title_01 {
    font-size: 12px; }
  .sub_title_0 {
    font-size: 13px; }
  .sub_title_1 {
    font-size: 14px; }
  .sub_title_2 {
    font-size: 16px; }

.medium_title {
  font-stretch: 500;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 600; }
  .medium_title_0 {
    font-size: 13px; }
  .medium_title_1 {
    font-size: 14px; }
  .medium_title_2 {
    font-size: 16px; }
  .medium_title_22 {
    font-size: 16px; }
  .medium_title_3 {
    font-size: 24px; }
  .medium_title_4 {
    font-size: 36px; }

.main__title {
  font-weight: 700;
  font-stretch: normal;
  font-style: normal; }
  .main__title_0 {
    font-size: 13px; }
  .main__title_1 {
    font-size: 14px; }
  .main__title_4 {
    font-size: 40px; }

.color_lime {
  color: #4a9396; }

.color_yellow {
  color: #fdd03b; }

.color_orange {
  color: #D57B00; }

.color_black {
  color: #00202e; }

.color_brown {
  color: #291907; }

.color_white {
  color: #fff; }

@media (max-width: 1370px) {
  .medium_title_4 {
    font-size: 30px; } }

@media (max-width: 500px) {
  .medium_title_3 {
    font-size: 18px; } }

@media (max-width: 385px) {
  .main__title_4 {
    font-size: 32px; } }
