.brief_st {
  margin-bottom: 70px; }

.form_title {
  font-size: 36px;
  font-weight: bold;
  line-height: 1.46;
  color: #302d2d; }

.open_file__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
  background: #F5F5F5;
  cursor: pointer;
  padding: 15px 45px; }

.hidden_value_file {
  position: absolute;
  height: 100%;
  opacity: 0;
  width: 100%; }

.input_file__block {
  position: relative; }

.clearfix:after, .checkbox-ios:after {
  content: "";
  display: table;
  clear: both;
  height: 0;
  visibility: hidden; }

.checkbox-ios {
  width: 75px;
  padding-top: 20px;
  margin-bottom: 10px; }
  .checkbox-ios__toggle {
    display: none; }
  .checkbox-ios__toggle + .checkbox-ios__label {
    display: block;
    position: relative;
    transition: .3s;
    border-radius: 50px;
    background-color: #4e4e4e;
    height: 30px;
    margin-bottom: 0;
    cursor: pointer; }
    .checkbox-ios__toggle + .checkbox-ios__label:before {
      transition: .3s;
      content: "";
      display: block;
      position: absolute;
      border-radius: 50%;
      background-color: #fff;
      width: 20px;
      height: 20px;
      top: 5px;
      left: 5px; }
  .checkbox-ios__value {
    display: block;
    float: left;
    width: 50%;
    font-size: 11px;
    text-align: center;
    margin-top: 35px;
    letter-spacing: 1px;
    line-height: 1.5; }
    .checkbox-ios__value.left {
      text-align: left;
      font-weight: bold; }
    .checkbox-ios__value.right {
      text-align: right; }
  .checkbox-ios__toggle:checked + .checkbox-ios__label {
    background-color: #2cc524; }
    .checkbox-ios__toggle:checked + .checkbox-ios__label:before {
      left: calc(100% - 20px - 5px); }
    .checkbox-ios__toggle:checked + .checkbox-ios__label .right {
      font-weight: bold; }
    .checkbox-ios__toggle:checked + .checkbox-ios__label .left {
      font-weight: normal; }
  .checkbox-ios--blue .checkbox-ios__toggle:checked + .checkbox-ios__label {
    background-color: #2a9db3; }
  .checkbox-ios--alert .checkbox-ios__toggle:checked + .checkbox-ios__label {
    background-color: #cf201b; }

@media screen and (max-width: 768px) {
  .feedback_smal {
    margin-bottom: 40px; }
    .feedback_smal .container-coll-3_bottom {
      grid-gap: 0; } }

@media screen and (max-width: 748px) {
  .form_col div:first-child {
    margin-top: 0; } }
