.main_section {
  position: relative; }
  .main_section_content__block {
    transition: all .2s;
    position: absolute;
    left: 0;
    padding: 15px 24px;
    width: 100%;
    bottom: -145px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background: #010b10;
    background: linear-gradient(0deg, #010b10 0%, rgba(1, 11, 16, 0.58) 61%, rgba(1, 11, 16, 0) 100%); }
    .main_section_content__block .link__block {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end; }
  .main_section_content_title {
    text-align: center;
    padding-bottom: 12px; }
  .main_section_img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
    .main_section_img img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .main_section_prof {
    position: relative;
    overflow: hidden;
    border-radius: 3px;
    cursor: pointer; }
    .main_section_prof:hover .main_section_content__block {
      bottom: 0;
      background: linear-gradient(0deg, rgba(1, 11, 16, 0.690914) 100%, rgba(1, 11, 16, 0) 100%);
      justify-content: flex-end; }
  .main_section__block {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    height: 400px; }

.link__block {
  transition: all .2s;
  display: flex;
  align-items: center;
  cursor: pointer; }
  .link__block img {
    position: relative;
    margin-left: 4px;
    transition: all .2s;
    right: 0; }
  .link__block:hover img {
    right: -5px; }

.main_section_prof_1 {
  grid-area: 1 / 1 / 3 / 3; }
  .main_section_prof_1 .main_section_content__block {
    bottom: -75px;
    height: unset; }

.main_section_prof_2 {
  grid-area: 1 / 3 / 2 / 4; }

.main_section_prof_3 {
  grid-area: 1 / 4 / 2 / 5; }

.main_section_prof_4 {
  grid-area: 2 / 3 / 3 / 4; }

.main_section_prof_5 {
  grid-area: 2 / 4 / 3 / 5; }

.section_title__block {
