  padding-bottom: 35px; }

.second_section_container {
  position: relative;
  width: 100%;
  height: 500px;
  background: transparent;
  border-radius: 3px; }
  .second_section_container:after {
    content: 'Доигрался?';
    z-index: 0;
    color: #D57B00;
    font-weight: 500;
    position: absolute;
    display: flex;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.second_section_container .clip {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: .4s; }

.second_section_container .clip.clip1 {
  clip-path: polygon(0 0, 55% 0, 20% 100%, 0% 100%); }

.second_section_container .clip.clip2 {
  clip-path: polygon(55% 0, 100% 0, 45% 100%, 20% 100%); }

.second_section_container .clip.clip3 {
  clip-path: polygon(100% 0, 100% 0%, 100% 100%, 45% 100%); }

.second_section_container:hover .clip {
  clip-path: polygon(100% 0, 100% 0%, 100% 100%, 100% 100%); }

.second_section_container .clip:hover {
  clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%); }

.content {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: -100%;
  left: 0;
  width: 70%;
  padding: 20px;
  opacity: 0;
  background: #fff;
  transition: .5s; }
  .content .link__block {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end; }

.title {
  font-weight: 700;
  padding-bottom: 5px; }

.second_section_container .clip:hover .content {
  bottom: 0;
  opacity: 1; }

.content_title__block {
