.four_section {
  position: relative;
  height: 100vh;
  padding-top: 60px; }
  .four_section_title {
    margin: 0 auto;
    text-align: center; }

.four_section .box {
  position: absolute;
  top: calc(50% - 200px);
  left: calc(50% - 200px);
  width: 400px;
  height: 400px;
  transform-style: preserve-3d; }

.four_section .box div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transform: rotateX(-20deg) rotateY(25deg) translate3d(-75px, -50px, 150px); }

.four_section .box div span {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background: #ccc; }

.four_section .box div span video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.bg_video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(20px); }

.four_section .box div span:nth-child(1) {
  transform: rotateX(0deg) translate3d(0, 0, 200px); }

.four_section .box div span:nth-child(2) {
  transform: rotateY(90deg) translate3d(0, 0, -200px); }

.four_section .box div span:nth-child(2) video {
  transform: rotateY(180deg); }

.four_section .box div span:nth-child(3) {
  transform: rotateX(90deg) translate3d(0, 0, 200px); }

